.select-trigger {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 12px;
    border: 1px solid #d1d5db;
    border-radius: 8px;
    background-color: white;
    cursor: pointer;
  }
  
  .select-content {
    position: absolute;
    width: 100%;
    margin-top: 4px;
    z-index: 10;
  }
  
  .chart-title {
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
  }
  